ound:linear-gradient(rgba(188,156,255,0.25) 0%, rgba(139,164,249,0.25) 100%);border-radius:0}.datepicker--cell.-current-.-in-range-:before{position:absolute;display:inline-block;content:'';height:100%;width:100%;left:0;z-index:-1;background:linear-gradient(#6FCF97 0%, #66D2EA 100%);opacity:.7;border-radius:50%}.datepicker--cell.-range-from-{font-weight:bold;color:white;background:linear-gradient(rgba(188,156,255,0.25) 0%, rgba(139,164,249,0.25) 100%);border-radius:50% 0 0 50%}.datepicker--cell.-range-from-:before{position:absolute;display:inline-block;content:'';height:100%;width:100%;left:0;z-index:-1;background:linear-gradient(#bc9cff 0%, #8ba4f9 100%);opacity:1;border-radius:50%}.datepicker--cell.-range-to-{font-weight:bold;color:white;background:linear-gradient(rgba(188,156,255,0.25) 0%, rgba(139,164,249,0.25) 100%);border-radius:0 50% 50% 0}.datepicker--cell.-range-to-:before{position:absolute;display:inline-block;content:'';height:100%;width:100%;left:0;z-index:-1;background:linear-gradient(#bc9cff 0%, #8ba4f9 100%);opacity:1;border-radius:50%}.datepicker--buttons{margin-top:20px;display:flex;justify-content:space-between}.datepicker--button{font-size:.85714rem;font-weight:bold;text-transform:uppercase;cursor:pointer}.datepicker--button-apply{color:#bc9cff}.datepicker.-from-top-{transform:translateY(-8px)}.datepicker.-from-right-{transform:translateX(8px)}.datepicker.-from-bottom-{transform:translateY(8px)}.datepicker.-from-left-{transform:translateX(-8px)}.datepicker.active{opacity:1;transform:translate(0);transition:opacity .3s ease, transform .3s ease, left 0s 0s}.datepicker-inline .datepicker{position:static;left:auto;right:auto;opacity:1;transform:none}.datepickers-container{position:absolute;left:0;top:0}
